import React, { Component } from 'react';
import './../css/App.css'

class Card extends Component {
  render() {
    return (
        <article className="cards--card">
           <img src={this.props.src} alt={this.props.name} />
            <h3>{this.props.name}</h3>
            <p>{this.props.desc}</p>
        </article>     
    );
  }
}



export default Card;



